@import "preset.scss";

.nav{
  display: none;
}

.chargeReady{
  background-color: #FFFFFF;
  z-index: 3;
  display: grid;
  grid-template-rows: 44px 1fr 130px;
}

.chargeSuccess{
  background-color: #FFFFFF;
  z-index: 1;
  display: grid;
  grid-template-rows: 44px 1fr 100px 100px;
}

.errorPage{
  background-color: #FFFFFF;
  z-index: 2;
  display: grid;
  grid-template-rows: 44px 1fr 100px;
}

.brochure{ 
  color: #231815;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin-bottom: 2.34vh;
  .explainContainer{
    display: grid;
    grid-template-columns: 1fr 1fr;
    width: 100%;
    img{
      justify-self: flex-end;
    }
    .kickboard{
      margin-right: 30px;
    }
    .chargePort{
      margin-right: 43px;
    }
    .firstExplain{
      margin-top: 21px;
    }
    .secondExplain{
      align-self: flex-end;
      margin-bottom: 21px;
    }
    
    &:nth-child(1){
      margin-bottom: 10.47vh;
    }
  }
  .split{
    position: absolute;
    width: 37.87vw;
    height: 0.5px;
    background-color: #C8C8C8;
    left: 50vw;
  }
}

.clockContainer{
  display: flex;
  flex-direction: column;
  align-items: center;
}

.clock{
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  font-size: 16px;
  font-weight: 500;
  width: 50px;
  height: 50px;
  background-color: $brand-color-yellow;
  border-radius: 50%;
  box-shadow: 0px 5px 15px rgba(255, 206, 0, 0.4);
  margin-bottom: 10px;
}

.clockText{
  font-size: 14px;
  font-weight: 400;
  color: #333333;
}

.chargeDisplayContainer{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 4.86vh
}
 
.plugContainer{
  div{
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 3px solid #FFCE00;
    width: 146px;
    height: 146px;
    div{
      background-color: #FFCE00;
      width: 118px;
      height: 118px;
      div{
        width: 32px;
        height: 32px;
        background-color: #FFFFFF;
      }
    }
  }
}

.footerButtonContainer{
  display: flex;
  justify-content: center;
}

.footerButton{
  border-radius: 30px;
  border: 1px solid #333333;
  width: 87.2vw;
  height: 60px;
  background-color: #FFFFFF;
  color: #333333;
  font-size: 16px;
  font-weight: 700;
  font-family: 'Noto Sans KR', sans-serif;
}

.errorDisplayContainer{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 24px;
  .errorHeadText{
    font-size: 24px;
    font-weight: 700;
    color: #222222;
  }
  .errorTextContainer{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #F9F9F9;
    width: 87.2vw;
    padding: 18px 0px;
    margin-top: 49px;
    border-radius: 8px;
    margin-bottom: 54px;
    .errorText{
      display: flex;
      flex-direction: column;
      align-items: center;
      font-size: 14px;
      font-weight: 400;
      color: #333333;
    }
  }
}